import { lineIp, hy_isCompress, hy_isEncrypt } from '../common/constants.js';

const hy_ajax = (opt) => {
    opt = opt || {};
    opt.url = opt.url || '';
    opt.data = opt.data || null;
    opt.method = opt.method || 'GET';
    opt.header = opt.header || {
		"hy_serviceName": opt.serviceName,
		"hy_appId": "com.haiyisoft.escp",
		"hy_isCompress": hy_isCompress,
		"hy_isEncrypt": hy_isEncrypt
    };
    opt.success = opt.success || function () {};

    uni.request({
        url: lineIp + opt.url,
        data: opt.data,
        method: opt.method,
        header: opt.header,
        dataType: 'json',
        success: function (res) {
			console.log(res);
			if(!res.data) {
				uni.showToast({
					icon: 'none',
					title: ''
				});
				return;
			}
			let responseData = JSON.parse(res.data.substr(0, res.data.length-4)); // data , data ;000, 
			console.log(responseData);
			// errorMessage
			if(responseData.content.errorMessage) {
				uni.showToast({
					icon: 'none',
					title: responseData.content.errorMessage,
				});
				return;
			}
			res.data = responseData; // data,
            opt.success(res);
        },
        fail: function (res) {
            uni.showToast({
                title: ''
            });
        },
		complete: () => {}
    })
}
const checkAppUpdate = function() {
	uni.request({
		url: 'http://kk0221.ngrok.xiaomiqiu.cn/version.json',
		data: {
			//
			appid: plus.runtime.appid,
			version: plus.runtime.version
		},
		success: res => {
			if (res.statusCode == 200 && res.data.status === 1) {
				const dcRichAlert = uni.requireNativePlugin('DCloud-RichAlert');
				dcRichAlert.show(
					{
						position: 'center',
						title: '',
						titleColor: '#FF0000',
						content: res.data.note,
						contentAlign: 'left',
						/* checkBox: {
							title: '',
							isSelected: true
						}, */
						buttons: [
							{
								title: ''
							},
							{
								title: ''
							},
							{
								title: ''
							}
						]
					},
					result => {
						switch (result.type) {
							case 'button':
								console.log('callback---button--' + result.index);
								if (result.index == 0) {
									uni.showModal({
										title: '',
										content: '',
										/**
										 * 
										 */
										showCancel: !this.forcedLogin,
										success: res => {
											if (res.confirm) {
											}
										}
									});
								} else if (result.index == 2) {
									plus.runtime.openURL(res.data.url);
								}
								break;
							case 'checkBox':
								console.log('callback---checkBox--' + result.isSelected);
								break;
							case 'a':
								console.log('callback---a--' + JSON.stringify(result));
								break;
							case 'backCancel':
								console.log('callback---backCancel--');
								break;
						}
					}
				);
			}
		},
		fail: () => {},
		complete: () => {}
	});
}
export {
    hy_ajax,
	checkAppUpdate
}



